<template>
    <div>
        <pdf
            v-for="i in numPages"
            :key="i"
            :src="src"
            :page="i"
            class="w-50 d-inline-block border"
        ></pdf>
    </div>
</template>
 
<script>
    import pdf from 'vue-pdf'
    var loadingTask = pdf.createLoadingTask('./pdf/JASEELPVRESUME.pdf');
    
    export default {
        components: {
            pdf
        },
        data() {
            return {
                src: loadingTask,
                numPages: undefined,
            }
        },
        mounted() {
            this.src.promise.then(pdf => {
                this.numPages = pdf.numPages;
            });
        }
    }
</script>